<template>
  <div class="timer-container">
    <div class="page-header">
      <h1>‚è∞ ËÆ°Êó∂Âô®</h1>
      <p class="page-description">ÂÄíËÆ°Êó∂Âô®ÔºåÊîØÊåÅËá™ÂÆö‰πâÊó∂Èó¥ÂíåÊèêÈÜí</p>
    </div>

    <div class="timer-section">
      <div class="timer-display">
        <div class="time-circles">
          <div class="time-circle">
            <div class="time-value">{{ formatTime(remainingTime).hours }}</div>
            <div class="time-label">Â∞èÊó∂</div>
          </div>
          <div class="time-separator">:</div>
          <div class="time-circle">
            <div class="time-value">{{ formatTime(remainingTime).minutes }}</div>
            <div class="time-label">ÂàÜÈíü</div>
          </div>
          <div class="time-separator">:</div>
          <div class="time-circle">
            <div class="time-value">{{ formatTime(remainingTime).seconds }}</div>
            <div class="time-label">Áßí</div>
          </div>
        </div>
        
        <div class="progress-ring">
          <svg width="200" height="200">
            <circle
              cx="100"
              cy="100"
              r="90"
              fill="none"
              stroke="#f0f0f0"
              stroke-width="8"
            />
            <circle
              cx="100"
              cy="100"
              r="90"
              fill="none"
              :stroke="isRunning ? '#409eff' : '#67c23a'"
              stroke-width="8"
              stroke-linecap="round"
              :stroke-dasharray="circumference"
              :stroke-dashoffset="strokeDashoffset"
              transform="rotate(-90 100 100)"
              class="progress-circle"
            />
          </svg>
          <div class="progress-text">
            <div class="progress-percentage">{{ progressPercentage }}%</div>
            <div class="progress-status">{{ statusText }}</div>
          </div>
        </div>
      </div>

      <div class="timer-controls">
        <div class="time-input" v-if="!isRunning && !isPaused">
          <h3>ËÆæÁΩÆÊó∂Èó¥</h3>
          <div class="input-group">
            <div class="input-item">
              <label>Â∞èÊó∂</label>
              <el-input-number v-model="inputHours" :min="0" :max="23" size="large"></el-input-number>
            </div>
            <div class="input-item">
              <label>ÂàÜÈíü</label>
              <el-input-number v-model="inputMinutes" :min="0" :max="59" size="large"></el-input-number>
            </div>
            <div class="input-item">
              <label>Áßí</label>
              <el-input-number v-model="inputSeconds" :min="0" :max="59" size="large"></el-input-number>
            </div>
          </div>
        </div>

        <div class="control-buttons">
          <el-button
            v-if="!isRunning && !isPaused"
            type="primary"
            size="large"
            @click="startTimer"
            :disabled="totalInputTime === 0"
          >
            <i class="el-icon-video-play"></i> ÂºÄÂßã
          </el-button>
          
          <el-button
            v-if="isRunning"
            type="warning"
            size="large"
            @click="pauseTimer"
          >
            <i class="el-icon-video-pause"></i> ÊöÇÂÅú
          </el-button>
          
          <el-button
            v-if="isPaused"
            type="primary"
            size="large"
            @click="resumeTimer"
          >
            <i class="el-icon-video-play"></i> ÁªßÁª≠
          </el-button>
          
          <el-button
            v-if="isRunning || isPaused"
            type="danger"
            size="large"
            @click="stopTimer"
          >
            <i class="el-icon-video-pause"></i> ÂÅúÊ≠¢
          </el-button>
          
          <el-button
            v-if="isRunning || isPaused"
            size="large"
            @click="resetTimer"
          >
            <i class="el-icon-refresh"></i> ÈáçÁΩÆ
          </el-button>
        </div>

        <div class="quick-times">
          <h3>Âø´ÈÄüËÆæÁΩÆ</h3>
          <div class="quick-buttons">
            <el-button size="small" @click="setQuickTime(1, 0, 0)">1ÂàÜÈíü</el-button>
            <el-button size="small" @click="setQuickTime(5, 0, 0)">5ÂàÜÈíü</el-button>
            <el-button size="small" @click="setQuickTime(10, 0, 0)">10ÂàÜÈíü</el-button>
            <el-button size="small" @click="setQuickTime(15, 0, 0)">15ÂàÜÈíü</el-button>
            <el-button size="small" @click="setQuickTime(30, 0, 0)">30ÂàÜÈíü</el-button>
            <el-button size="small" @click="setQuickTime(0, 1, 0)">1Â∞èÊó∂</el-button>
          </div>
        </div>
      </div>
    </div>

    <!-- ÂÆåÊàêÊèêÁ§∫ -->
    <el-dialog
      title="‚è∞ Êó∂Èó¥Âà∞ÔºÅ"
      :visible.sync="showCompleteDialog"
      width="400px"
      center
    >
      <div style="text-align: center; padding: 20px;">
        <div style="font-size: 48px; margin-bottom: 20px;">üéâ</div>
        <p style="font-size: 18px; color: #2c3e50;">ËÆ°Êó∂Âô®Â∑≤ÂÆåÊàêÔºÅ</p>
        <p style="color: #7f8c8d;">ËÆæÂÆöÊó∂Èó¥: {{ formatTotalTime(originalTime) }}</p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showCompleteDialog = false">Á°ÆÂÆö</el-button>
        <el-button type="primary" @click="restartTimer">ÈáçÊñ∞ÂºÄÂßã</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'TimerView',
  data() {
    return {
      inputHours: 0,
      inputMinutes: 5,
      inputSeconds: 0,
      remainingTime: 0,
      originalTime: 0,
      isRunning: false,
      isPaused: false,
      timer: null,
      showCompleteDialog: false,
      circumference: 2 * Math.PI * 90
    }
  },
  computed: {
    totalInputTime() {
      return this.inputHours * 3600 + this.inputMinutes * 60 + this.inputSeconds
    },
    
    progressPercentage() {
      if (this.originalTime === 0) return 100
      return Math.round(((this.originalTime - this.remainingTime) / this.originalTime) * 100)
    },
    
    strokeDashoffset() {
      if (this.originalTime === 0) return this.circumference
      const progress = (this.originalTime - this.remainingTime) / this.originalTime
      return this.circumference * (1 - progress)
    },
    
    statusText() {
      if (this.isRunning) return 'ËøêË°å‰∏≠'
      if (this.isPaused) return 'Â∑≤ÊöÇÂÅú'
      if (this.remainingTime === 0 && this.originalTime > 0) return 'Â∑≤ÂÆåÊàê'
      return 'ÂæÖÂºÄÂßã'
    }
  },
  methods: {
    formatTime(seconds) {
      const hours = Math.floor(seconds / 3600)
      const minutes = Math.floor((seconds % 3600) / 60)
      const secs = seconds % 60
      
      return {
        hours: hours.toString().padStart(2, '0'),
        minutes: minutes.toString().padStart(2, '0'),
        seconds: secs.toString().padStart(2, '0')
      }
    },
    
    formatTotalTime(seconds) {
      const time = this.formatTime(seconds)
      return `${time.hours}:${time.minutes}:${time.seconds}`
    },
    
    startTimer() {
      if (this.totalInputTime === 0) return
      
      this.remainingTime = this.totalInputTime
      this.originalTime = this.totalInputTime
      this.isRunning = true
      this.isPaused = false
      
      this.timer = setInterval(() => {
        if (this.remainingTime > 0) {
          this.remainingTime--
        } else {
          this.completeTimer()
        }
      }, 1000)
    },
    
    pauseTimer() {
      this.isRunning = false
      this.isPaused = true
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
    },
    
    resumeTimer() {
      this.isRunning = true
      this.isPaused = false
      
      this.timer = setInterval(() => {
        if (this.remainingTime > 0) {
          this.remainingTime--
        } else {
          this.completeTimer()
        }
      }, 1000)
    },
    
    stopTimer() {
      this.isRunning = false
      this.isPaused = false
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      this.remainingTime = 0
      this.originalTime = 0
    },
    
    resetTimer() {
      this.isRunning = false
      this.isPaused = false
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      this.remainingTime = this.originalTime
    },
    
    completeTimer() {
      this.isRunning = false
      this.isPaused = false
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      
      // Êí≠ÊîæÊèêÁ§∫Èü≥
      this.playNotificationSound()
      
      // ÊòæÁ§∫ÂÆåÊàêÂØπËØùÊ°Ü
      this.showCompleteDialog = true
      
      // ÊµèËßàÂô®ÈÄöÁü•
      this.showNotification()
    },
    
    restartTimer() {
      this.showCompleteDialog = false
      this.remainingTime = this.originalTime
      this.startTimer()
    },
    
    setQuickTime(minutes, hours = 0, seconds = 0) {
      if (this.isRunning || this.isPaused) return
      
      this.inputHours = hours
      this.inputMinutes = minutes
      this.inputSeconds = seconds
    },
    
    playNotificationSound() {
      // ÂàõÂª∫Èü≥È¢ë‰∏ä‰∏ãÊñáÊí≠ÊîæÊèêÁ§∫Èü≥
      try {
        const audioContext = new (window.AudioContext || window.webkitAudioContext)()
        const oscillator = audioContext.createOscillator()
        const gainNode = audioContext.createGain()
        
        oscillator.connect(gainNode)
        gainNode.connect(audioContext.destination)
        
        oscillator.frequency.setValueAtTime(800, audioContext.currentTime)
        gainNode.gain.setValueAtTime(0.3, audioContext.currentTime)
        gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 1)
        
        oscillator.start(audioContext.currentTime)
        oscillator.stop(audioContext.currentTime + 1)
      } catch (error) {
        console.log('Êó†Ê≥ïÊí≠ÊîæÊèêÁ§∫Èü≥:', error)
      }
    },
    
    showNotification() {
      if ('Notification' in window && Notification.permission === 'granted') {
        new Notification('ËÆ°Êó∂Âô®ÂÆåÊàê', {
          body: `ËÆæÂÆöÊó∂Èó¥ ${this.formatTotalTime(this.originalTime)} Â∑≤Âà∞ÔºÅ`,
          icon: '/favicon.ico'
        })
      } else if ('Notification' in window && Notification.permission !== 'denied') {
        Notification.requestPermission().then(permission => {
          if (permission === 'granted') {
            new Notification('ËÆ°Êó∂Âô®ÂÆåÊàê', {
              body: `ËÆæÂÆöÊó∂Èó¥ ${this.formatTotalTime(this.originalTime)} Â∑≤Âà∞ÔºÅ`,
              icon: '/favicon.ico'
            })
          }
        })
      }
    }
  },
  
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer)
    }
  }
}
</script>

